section#join,
section#account-find {
    padding-top: 120px;

    @media (min-width: 768px) {
        padding-top: 200px;
    }
    @media (min-width: 1366px) {
        padding-top: 300px;
    }

    article.join--container {
        width: 100%;
        max-width: $defaultMaxWidth;
        margin: 0 auto;
        padding: 0 1rem;
        display: flex;
        flex-wrap: wrap;

        .info--wrap {
            width: 100%;

            @media (min-width: 1024px) {
                width: 50%;
            }

            .box {
                max-width: 400px;
                margin: 0 auto;

                &.responsive--line {
                    @media (max-width: 1023px) {
                        padding-top: 5rem;
                        border-top: 1px solid rgba(0, 0, 0, 0.1);
                        margin-top: 5rem;
                    }
                }

                h4 {
                    text-align: center;
                    font-weight: bold;
                    margin-bottom: 3rem;
                }

                .group {
                    display: flex;
                    margin-bottom: 1.25rem;

                    @media (min-width: 768px) {
                        margin-bottom: 1.5rem;
                    }

                    &.wrap {
                        flex-wrap: wrap;
                    }

                    &.direction-column {
                        flex-direction: column;
                    }

                    &.align-start {
                        align-items: flex-start;
                    }

                    &.margin--top-64px {
                        .group.mb-07 {
                            a {
                                color: rgba(128, 211, 142, 1);
                                margin-left: 1rem;
                                margin-bottom: -5px;
                                font-size: 10px;
                            }
                        }

                        p.check--error {
                            display: none;
                            font-size: 10px;
                            color: red;

                            &.error {
                                display: block;
                            }
                        }
                        @media (min-width: 1024px) {
                            margin-top: 64px !important;
                        }
                    }

                    &.password--success {
                        p.success--comment {
                            text-align: center;
                            margin-bottom: 2rem;
                        }

                        .success--login-btn {
                            background-color: white;
                            color: rgba(128, 211, 142, 1);
                            border: 1px solid gray;
                            max-width: initial !important;
                            width: 100% !important;
                            margin-left: 0 !important;
                            margin-top: 1rem !important;
                        }
                    }

                    & > .group {
                        margin-bottom: 0;
                    }

                    label.number--box {
                        margin-left: 1.5%;
                    }

                    .line {
                        width: 100%;
                        height: 10px;
                        background-color: rgb(62, 62, 62);
                        margin-bottom: 0.7rem;

                        & ~ label {
                            margin-bottom: 0.7rem;
                        }

                        & ~ span {
                            font-size: 10px;
                            color: rgba(0, 0, 0, 0.5);

                            @media (min-width: 768px) {
                                font-size: 11px;
                            }
                        }
                    }
                    p {
                        font-size: 12px;
                        a {
                            font-size: 13px;
                            color: rgba(128, 211, 142, 1);
                            margin-left: 1rem;
                        }
                    }

                    .found--box {
                        .comment {
                            font-size: 14px;
                            margin-bottom: 0.7rem;

                            @media (min-width: 768px) {
                                font-size: 15px;
                            }
                        }
                    }

                    .go-to-login {
                        font-size: 13px;
                        color: rgba(128, 211, 142, 1);
                    }
                }
            }
        }
    }
}
